@use 'colors' as *;
@use 'header' as *;
@use 'buttons' as *;
@use 'menu' as *;

.App {
 position: absolute;
 z-index: -10;
 width: 100%;
 height: 100%;
}

.Background {
 position: absolute;
 width: 100%;
 height: 100%;
 background: linear-gradient(
  -0.1turn,
  rgba(0, 7, 13, 1),
  rgba(7, 39, 70, 1),
  rgba(65, 95, 126, 1)
 );
 box-shadow: inset -27px 32px 34px rgba(0, 0, 0, 0.25%),
  inset 1px 1px 19px rgba(255, 255, 255, 0.25);
 overflow-y: hidden;
 overflow-x: hidden;
}

.highlight path {
 color: $lightOrange;
 filter: drop-shadow(2px 2px 25px $lightOrange);
}

.hidden {
 display: none;
}
